import React from 'react';
import d from './../Dialogs.module.css';
import {NavLink} from "react-router-dom";
import {parseServerDate} from "../Dialogs";

const Message = (props) => {

    const added = parseServerDate(props.addedAt);
    const shortMsg = (props.message.length > 70) ? props.message.slice(0, 70) + '...' : props.message;
    return (
        <div className={d.msgCont}>
            <div className={(props.lastMessageIsMine) ? d.meSenderTitle : d.senderTitle}>
                <div>{props.senderName}:</div>
                <div className={d.msgTime}>{added.date} {added.time}</div>
                <div className={(props.lastMessageIsMine) ? d.msgText +' '+ d.myMsgText : d.msgText}>- {shortMsg}</div>
            </div>

        </div>
    );
}


export default Message;