import {DialogsApi} from "../api/api";

const ADD_MSG = 'ADD_MSG';
const SET_DIALOGS = 'SET_DIALOGS';
const SET_MESSAGES = 'SET_MESSAGES';
const SET_NEW_MESSAGES_COUNT = 'SET_NEW_MESSAGES_COUNT';
const SET_IS_ADDING_MSG = 'SET_IS_ADDING_MSG';

export const addMsgActionCreator = (message) => ({type: ADD_MSG, message});
export const setDialogs = (dialogs) => ({type: SET_DIALOGS, dialogs});
export const setMessages = (messages) => ({type: SET_MESSAGES, messages});
export const setNewMessagesCount = (count) => ({type: SET_NEW_MESSAGES_COUNT, count});
export const setIsAddingMsg = (is) => ({type: SET_IS_ADDING_MSG, is});
// thunk

export const getAllDialogs = () => {
    /*[{
        hasNewMessages: false
        id: 25643
        lastDialogActivityDate: "2022-10-19T17:28:12.537"
        lastUserActivityDate: "2022-10-19T06:55:25.787"
        newMessagesCount: 0
        photos: Object { small: "https://social-network.samuraijs.com/activecontent/images/users/25643/user-small.jpg?v=1",
         large: "https://social-network.samuraijs.com/activecontent/images/users/25643/user.jpg?v=1" }
        userName: "MashaKustova"}, ......]
    * */
    return dispatch => {
        DialogsApi.GetAllDialogs().then(res => {
            if (res.data && res.data.length > 0) {
                dispatch(setDialogs(res.data));

                res.data.map(dialog=>{
                 dispatch(getMessagesList(dialog.id));
                })
            }
        });
    }
}
