import React from "react";
import d from "./Dialogs.module.css";
import {parseServerDate} from "./Dialogs";
import {NavLink, useHistory} from "react-router-dom";
import backImg from "../../assets/images/back.png";
import noLogo from "../../assets/images/smallNoImages.jpg"
import {maxLengthCreator, required} from "../../utils/validators/FormValidator";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../common/FormControls/FormControls";
import Preloader from "../common/Preloader/Preloader";

const ChatWithFriend = ({messages, dialogs, friendId, profile, ...props}) => {

    const history = useHistory();
    let chatMsg = messages.filter(message => (message.recipientId == friendId || message.senderId == friendId));
    // console.log(chatMsg, 'chat msg');

    let chatDialog = dialogs.find(dialog => dialog.id == friendId);

    const myAva = (profile && profile.photos.small) ? profile.photos.small : noLogo;
    const friendAva = (chatDialog && chatDialog.photos.small) ? chatDialog.photos.small : noLogo;

    let maxLength150 = maxLengthCreator(150);

    const AddMessageForm = (props) => {

        const handleKeyPress = e => {
            if (e.keyCode == 13 && e.shiftKey == false) {
                props.handleSubmit()
            }
        };

        return (
            <form action="" onSubmit={props.handleSubmit}>
                <div className={d.textarea}>
                    <Field component={Textarea} onKeyDown={handleKeyPress}
                           validate={[required, maxLength150]}
                           name='message'
                           placeholder={'type your message here'}/>
                </div>
                <div className={d.addPostButtonCont}>
                    <button className={d.addPostButton}></button>
                </div>
            </form>
        )
    };


    const AddMessageFormRedux = reduxForm({
        form: 'AddMessageForm',
    })(AddMessageForm);

    const onSubmit = (messages) => {
        console.log(messages);
        props.sendMessage(friendId, messages.message);
        //26376
    };

    if (props.isAddingMsg) {
        return <Preloader/>
    }
    return (
        <div className={d.chatCommon}>
            <div onClick={() => history.push('/dialogs')} title={'return to all dialogs'}>
                <img className={d.backImg} src={backImg} alt="Return to all dialogs"/>
            </div>

            {chatMsg.map(item => {
                return (
                    <div className={d.chatCont} key={item.id}>
                        {/*lastMessageIsMine={(messages.recipientId == dialogs.id) ? true : false}*/}

                        {/*my message*/}
                        {(friendId == item.recipientId) ?
                            <div className={d.myChatItem}>
                                <div className={d.frChatHeader}>
                                    <div className={d.frAva}>
                                        <img src={myAva} className={d.imgLogo} alt="my img"/>
                                    </div>
                                    <div className={d.titleData}>
                                        <div className={d.meSenderTitle}>{item.senderName}:</div>
                                        <div className={d.msgTime}>
                                            {parseServerDate(item.addedAt).date + ' ' +
                                          parseServerDate(item.addedAt).time}</div>
                                    </div>
                                </div>
                                <div style={{alignSelf: 'flex-end'}}
                                     className={(item.viewed) ? d.msgText + ' ' + d.myMsgText : d.msgText}>- {item.body}</div>
                            </div>
                            :
                            // friend's message
                            <div className={d.frChatItem}>
                                <div className={d.frChatHeader}>
                                    <div className={d.frAva}>
                                        <NavLink to={`/profile/${friendId}`}>
                                            <img src={friendAva} className={d.imgLogo} alt="friend img"/>
                                        </NavLink>
                                    </div>
                                    <div className={d.titleData}>
                                        <div className={d.senderTitle}>{item.senderName}:</div>
                                        <div className={d.msgTime}>{parseServerDate(item.addedAt).date + ' ' + parseServerDate(item.addedAt).time}</div>
                                    </div>
                                </div>
                                <div style={{alignSelf: 'flex-start'}} className={d.msgText}>- {item.body}</div>
                            </div>
                        }

                    </div>
                )
            })
            }

            <div className={d.addDialogDiv}>
                <AddMessageFormRedux onSubmit={onSubmit}/>
            </div>
        </div>
    )
}

export default ChatWithFriend;