import React from 'react';
import d from './../Dialogs.module.css';
import {NavLink} from "react-router-dom";
import noLogo from "../../../assets/images/smallNoImages.jpg"
import {parseServerDate} from "../Dialogs";

const Dialog = (props) => {
    /*lastDialogActivityDate(pin): "2022-10-20T14:41:48.703"
    lastUserActivityDate(pin): "2022-10-20T17:43:19.193"*/
    const logoSmall = (props.photos.small) ? props.photos.small : noLogo;


    const lastDialogActivity = parseServerDate(props.lastDialogActivityDate);
    const lastUserActivity = parseServerDate(props.lastUserActivityDate);


    return (
        <div className={(props.active === 'true') ? d.item + ' ' + d.active : d.item}>
            <NavLink style={{textDecoration: 'none'}} to={'/dialogs/' + props.id}>
                <div className={d.title}>{props.name}</div>
                <div>
                    <img className={d.imgLogo} src={logoSmall} alt="avatar"/>
                </div>
                <div className={d.date}>
                    {lastDialogActivity.date} {lastDialogActivity.time}
                </div>

            </NavLink>
        </div>
    );
}

export default Dialog;