import React, {useEffect} from 'react';
import d from './Dialogs.module.css';
import Dialog from "./Dialog/Dialog";
import Message from "./Message/Message";
import ChatWithFriend from "./ChatWithFriend";

export const parseServerDate = (date) => {
    let yymmdd = date.slice(0, date.indexOf("T")); //2022-10-20
    let [yy, dd, mm] = yymmdd.split('-');
    let usefulldate = [dd, mm, yy].join('-')
    let time = date.slice(date.indexOf("T") + 1, -4); //17:43:19
    return {date: usefulldate, time}
}


const Dialogs = (props) => {

    let {dialogs, messages, urlFriendId: friendId, profile} = props;
    /*[{
       hasNewMessages: false
       id: 25643
       lastDialogActivityDate: "2022-10-19T17:28:12.537"
       lastUserActivityDate: "2022-10-19T06:55:25.787"
       newMessagesCount: 0
       photos: Object { small: "https://social-network.samuraijs.com/activecontent/images/users/25643/user-small.jpg?v=1",
        large: "https://social-network.samuraijs.com/activecontent/images/users/25643/user.jpg?v=1" }
       userName: "MashaKustova"}, ......]
   * */


    /* try to configure sweet from shit*/
    let lastMsg = [];
    for (let item of dialogs) {
        let lastMsgFind = messages.filter(message => (message.senderId === item.id || message.recipientId === item.id));
        if (lastMsgFind.length) {
            lastMsg.push({messages: lastMsgFind.pop(), dialogs: item})
        }
    }
    // console.log(lastMsg, 'lst msg');
    const renderSweetFromShit = lastMsg.map(({messages, dialogs}) => {
        return (
            //senderName(pin): "EscapeFromHell"
            // recipientId(pin): 18684
            <div key={dialogs.id + messages.id} className={d.allChats}>
                <Dialog key={dialogs.id} id={dialogs.id} name={dialogs.userName} {...dialogs}/>
                <Message key={messages.id} message={messages.body}  senderName={messages.senderName}
                         lastMessageIsMine={(messages.recipientId == dialogs.id) ? true : false}
                         addedAt={messages.addedAt}/>
            </div>
        )
    });

    //      
    //     . ,   
    if (friendId) {
     return   <ChatWithFriend friendId={friendId} messages={messages} dialogs={dialogs} profile={profile}
     sendMessage={props.sendMessage} isAddingMsg={props.isAddingMsg}/>
    };



    //      
    return (
        <div>
            <div className={d.dialogs}>
                <div className={d.dialogsItems}>
                    {renderSweetFromShit}
                </div>
            </div>

        </div>
    );
}

export default Dialogs;