import React, {useEffect} from 'react';
import {
    addMsgActionCreator as addMsg, countNewMessages, getAllDialogs,
    getDialogWithFriend,
    getMessagesList,
    sendMessage
} from "../../redux/dialogsReducer";
import Dialogs from "./Dialogs";
import {connect} from "react-redux";
import {withAuthRedirect} from "../hoc/withAuthRedirect";
import {compose} from "redux";
import {withRouter} from "react-router-dom";

const DialogsContainer = (props) => {



    useEffect(() => {
        props.getAllDialogs();
        props.countNewMessages();
        // props.getMessagesList(25643);
        // console.log(props.match.params.friendId, 'params');
    },[]);

    const urlFriendId = props.match.params.friendId;
    return (
        <div>
            <Dialogs urlFriendId={urlFriendId} { ...props}/>
        </div>
    )
}

const mapStateToProps = (state) => {
    return (
        {
            dialogs: state.dialogsPage.dialogs,
            messages: state.dialogsPage.messages,
            profile: state.profilePage.profile,
            isAddingMsg: state.dialogsPage.isAddingMsg,
        }
    )
};



export default compose(connect (mapStateToProps,
    {addMsg, getAllDialogs, getDialogWithFriend, getMessagesList, sendMessage, countNewMessages}),
    withRouter,
    withAuthRedirect)(DialogsContainer)


